<template>
    <div class="order">
        <div class="order__price">实付金额<b>￥66</b></div>
        <div class="order__btn" @click="() => handleShowConfirmChange(true)">提交订单</div>
    </div>
    <div class="mask" v-show="showConfirm" @click="() => handleShowConfirmChange(false)">
        <div class="mask__content">
            <h3 class="mask__content__title">确认要离开收银台？</h3>
            <p class="mask__content__desc">请尽快完成支付，否则将被取消</p>
            <div class="mask__content__btns">
                <div class="mask__content__btn mask__content__btn--first" @click="() => handleConfirmOrder(false)">取消订单</div>
                <div class="mask__content__btn mask__content__btn--last" @click="() => handleConfirmOrder(true)">确认支付</div>
            </div>
        </div>
    </div>
</template>

<script>
import { ref } from 'vue'
import { useRouter } from 'vue-router'
export default {
  name: 'Order',
  setup () {
    const showConfirm = ref(false)
    const router = useRouter()
    const handleShowConfirmChange = (status) => {
      showConfirm.value = status
    }
    const handleConfirmOrder = (status) => {
      if (status) {
        console.log('下单啦')
        router.push({ name: 'OrderList' })
      } else {
        console.log('取消订单')
      }
    }
    return { showConfirm, handleShowConfirmChange, handleConfirmOrder }
  }
}
</script>

