<template>
    <div class="wrapper">
        <div class="title">我的订单</div>
        <div class="orders">
            <div class="order"
            v-for="(item,index) in list"
            :key="index"
            @click="handleClickOrderList"
            >
                <div class="order__title">{{item.shopName}}
                    <span class="order__status">已取消</span>
                </div>
                <div class="order__content">
                    <div class="order__content__imgs">
                        <template
                        v-for="(innerItem, innerIndex) in item.products"
                        :key="innerIndex">
                            <img class="order__content__img"
                            :src="innerItem.product.img"
                            v-if="innerIndex <= 3"/>
                        </template>
                    </div>
                    <div class="order__content__info">
                        <div class="order__content__price">￥{{item.totalPrice}}</div>
                        <div class="order__content__count">共 {{item.totalNum}}件</div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <Docker :currentIndex="2"/>
</template>

<script>
import { get } from '../../utils/request'
import Docker from '../../components/Docker'
import { reactive, toRefs } from '@vue/reactivity'
const handleClickOrderList = () => {
  console.log('详情')
}
const useOrderListEffect = () => {
  const data = reactive({ list: [] })
  const getNearbyList = async () => {
    const result = await get('/api/order')
    if (result.code === 200 && result?.data?.length) {
      const orderList = result.data
      orderList.forEach(order => {
        const products = order.products || []
        let totalPrice = 0
        let totalNum = 0
        products.forEach(productItem => {
          totalNum += (productItem?.orderSales || 0)
          totalPrice += ((productItem?.product.price * productItem?.orderSales) || 0)
        })
        order.totalPrice = totalPrice
        order.totalNum = totalNum
      })
      data.list = result.data
      console.log(data.list)
    }
  }
  getNearbyList()
  const { list } = toRefs(data)
  return { list }
}

export default {
  name: 'OrderList',
  components: { Docker },
  setup () {
    const { list } = useOrderListEffect()
    return { list, handleClickOrderList }
  }
}
</script>

